.inputHelper {
  min-width: 100%;
}

.inputHelper__item {
  background: #fff;
  padding: 5px 25px 5px 10px;
  position: relative;
}

.inputHelper__item:hover {
  color: #fff;
  background: gray;
  cursor: pointer;
}

.inputHelper__text {
  margin: 0;
}

.inputHelper__item-delete {
  position: absolute;
  right: 5px;
  top: 5px;
  font-size: 18px;
}
